.nav_btn {
  display: inline-block;
  box-sizing: border-box;
  padding: 6px 12px;
  margin-top: 21px;
  margin-right: 85px;
  border: 1px solid black;
  border-radius: 3px;

  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  letter-spacing: 1px;
  color: var(--black);
  text-transform: uppercase;

  cursor: pointer;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.nav_btn::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 0%;
  height: 100%;
  background: black;
  transition: 0.3s linear;
  z-index: -1;
}

.nav_btn:hover::before {
  width: 100%;
  left: 0;
}

.nav_btn:hover {
  color: #fff;
}
